<template>
  <div id="module11-router">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>会员套餐</p>
    </header>
    <div class="content">
      <img
        v-if="this.$route.query.id == 10"
        src="../../assets/imgs/images/number1.png"
      />
      <img
        v-if="this.$route.query.id == 30"
        src="../../assets/imgs/images/number1.png"
      />
      <div v-if="this.$route.query.id == 27">
        <img src="../../assets/imgs/images/number3(1).jpg" />
        <img src="../../assets/imgs/images/number3(2).jpg" />
      </div>
    </div>
    <div class="button">
      <p>
        <span>￥</span>
        <span class="price">{{ bookPriceInfo.price / 100 }}</span>
        <span class="book">
          <span>{{ bookPriceInfo.name }}</span>
          <span>价值{{ bookPriceInfo.line_price / 100 }}元</span>
        </span>
      </p>
      <button>立即开通</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bookPriceInfo: {},
    };
  },
  created() {
    this.$nextTick(()=>{
      this.getPageData();
    })
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getPageData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getVipDetail",
        data: {
          id: this.$route.query.id,
        },
      });
      this.bookPriceInfo = data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#module11-router {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    img {
      width: 15px;
    }
    p {
      width: 100%;
      font-size: 18px;
      text-align: center;
    }
  }
  .content {
    margin-top: 5px;
    margin-bottom: 70px;
    padding: 0 15px;
    img {
      width: 100%;
    }
  }
  .button {
    padding: 0 15px;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-top: 1px solid var(--theme-gray);
    p {
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
      color: #ff4900;
      font-weight: bold;
      .price {
        font-size: 30px;
      }
      .book {
        margin-left: 10px;
        font-weight: normal;
        display: flex;
        flex-direction: column;
        span:first-child {
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          background-color: #ff4900;
          color: #fff;
          padding: 2px 3px;
          border-radius: 5px;
        }
        span:last-child {
          margin-top: 5px;
          color: var(--theme-gray);
          text-decoration: line-through;
        }
      }
    }
    button {
      margin: 5px 10px;
      width: 40%;
      border-radius: 999px;
      height: 50px;
      padding: 8px 0;
      color: #fff;
      border: 0px;
      background-color: #ff4900;
    }
  }
}
</style>